#title {
  position: absolute;
  top: 45%;
  left: 50%;
  width: max-content;
  transform: translate(-50%, -50%);
  font-size: 72px;
  background-size: 400%;
  background-position: var(--xy);
  background-image: repeating-linear-gradient(
    30deg,
    red,
    rgb(255, 107, 139) 8%,
    rgb(14, 230, 129) 16%,
    rgb(32, 148, 194) 24%,
    rgb(57, 57, 255) 32%,
    rgb(204, 12, 243) 40%,
    rgb(255, 0, 0) 48%
  );

  -moz-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

@media (max-width: 800px) {
  #titlecontainer {
    height: 90vh;
    position: relative;
  }
  #title {
    position: absolute;
    top: 45%;
    left: 50%;
    width: max-content;
    transform: translate(-50%, -50%);
    font-size: 40px;
    background-size: 400%;
    background-position: var(--xy);
    background-image: repeating-linear-gradient(
      30deg,
      red,
      rgb(255, 107, 139) 8%,
      rgb(14, 230, 129) 16%,
      rgb(32, 148, 194) 24%,
      rgb(57, 57, 255) 32%,
      rgb(204, 12, 243) 40%,
      rgb(255, 0, 0) 48%
    );
    -moz-background-clip: text;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
  }
}

@media (max-width: 340px) {
