* {
  user-select: none;
}

.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.nav {
  height: 8vh;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
  font-size: 18px;
  color: rgb(255, 255, 255);
  background-color: rgb(216, 237, 255);
  box-shadow: 0px 2px 2px rgb(141, 141, 129);
}

.nav:hover {
  background-color: rgb(233, 242, 250);
}

.navlink {
  margin: 0px 5px;
  color: rgb(17, 0, 255);
  text-decoration: none;
}

.activeNavlink {
  margin: 0px 5px;
  color: rgb(0, 160, 0);
  transform: scale(1.15);
  pointer-events: none;
}

.navlink:hover {
  animation: shake 0.2s forwards;
}

.topbar {
  height: 7vh;
  display: flex;
  padding: 10px;
  align-items: center;
  justify-content: space-around;
  background-color: rgb(216, 255, 248);
  border-top: 1px solid;
  border-bottom: 1px solid;
}

.canvas {
  height: 85vh;
  width: 100%;
  background-color: rgb(226, 210, 255);
}

@keyframes shake {
  0% {
    transform: skew(0deg);
  }
  30% {
    transform: skew(20deg);
  }
  50% {
    transform: skew(0deg);
  }
  70% {
    transform: skew(-20deg);
  }
  100% {
    transform: skew(0deg) scale(1.1);
  }
}

#speed {
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
  background-color: transparent;
}

#speed::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 5px;
  background: linear-gradient(90deg, green, blue, red);
}

#speed::-webkit-slider-thumb {
  -webkit-appearance: none;
  transform: translateY(-8px);
  border-radius: 5px;
  height: 20px;
  width: 10px;
  background-color: rgb(255, 0, 0);
}

#speed::-moz-range-track {
  -webkit-appearance: none;
  height: 5px;
  background: linear-gradient(90deg, green, blue, red);
}

#speed::-moz-range-thumb {
  -webkit-appearance: none;
  border-radius: 5px;
  height: 20px;
  width: 10px;
  background-color: rgb(255, 5, 5);
}

#speed::-ms-track {
  -webkit-appearance: none;
  height: 5px;
  background: linear-gradient(90deg, green, blue, red);
}

#speed::-ms-thumb {
  -webkit-appearance: none;
  border-radius: 5px;
  height: 20px;
  width: 10px;
  background-color: rgb(255, 5, 5);
}

.btn {
  height: 80%;
  border: none;
  outline: none;
  padding: 10px;
  color: white;
  cursor: pointer;
  font-weight: bold;
  padding: 0px 10px;
  border-radius: 5px;
  border: 1px solid blue;
  background-color: rgb(42, 170, 255);
}

.btn:active {
  transform: scale(0.95);
}

.btn:disabled {
  height: 5vh;
  padding: 5px;
  outline: none;
  cursor: default;
  border: 1px solid;
  padding: 0px 10px;
  border-radius: 5px;
  color: rgb(7, 4, 4);
  background-color: rgb(165, 192, 209);
}

@media (max-width: 700px) {
  .nav {
    height: auto;
    width: 100vw;
  }

  .navlink {
    margin: 5px 5px;
  }

  .activeNavlink {
    margin: 5px 5px;
  }
}
